.Hero {
  padding: 140px 0 0 0;
  background: $blanco;
  background-image: url('../img/promo/fondo.jpg');
  background-repeat: repeat;
  background-size: cover;
  background-position: center;
  &__wrapper-title {
    text-align: center;
    img {
      width: 100%;
      max-width: 465px;
      margin: 0 auto;
      margin-bottom: 30px; } }
  &__wrapper-mobile-form {
    display: none;
